import React from 'react';
import styles from 'src/styles/developers.module.css';
import { Helmet } from 'react-helmet';
import Header from 'src/components/Header.jsx';
import Config from 'src/Config.js';

class Developer extends React.Component {
    genTitle(i) {
        switch (i) {
            case 'debug':
                return '–†–∞–∑—Ä–∞–±–æ—Ç—á–∏–∫ —Å–∞–π—Ç–∞';
                break;
            
            case 'masero':
                return '–†–∞–∑—Ä–∞–±–æ—Ç—á–∏–∫ –±–æ—Ç–∞'
                break;
        };
    };

    render() {
        return (
            <div className={`${styles.developer} ${styles[this.props.i]}`} title={this.genTitle(this.props.i)}>
                <div className={styles.iconWrapper}>
                    <img src={this.props.AvatarURL} className={styles.icon} alt="" />
                </div>
                <h1 className={styles.name}>
                    {this.props.name}
                </h1>
            </div>
        );
    };
};

class DevelopersList extends React.Component {
    render() {
        return (
            <div className={styles.developers}>
                <div className={styles.developersContent}>
                    {this.props.children}
                </div>
            </div>
        );
    };
};

class Developers extends React.Component {
    render() {
        return (
            <div className={styles.root}>
                <Helmet>
                    <title>
                        {Config.SiteData.Title}–†–∞–∑—Ä–∞–±–æ—Ç—á–∏–∫–∏
                    </title>
                    <link rel="shortcut icon" href={Config.SiteData.IconURL} type="image/x-icon"/>
                    <meta name="viewport" content="width=device-width, initial-scale=1.0"/>
                </Helmet>
                <Header />
                <DevelopersList>
                    <Developer name="Debug_pro" i="debug" AvatarURL={Config.Users.Debug.AvatarURL} />
                    <Developer name="–ê–ª—ã–π –ø—Ä–∏–Ω—Ü –ú–∞—Å–µ—Ä–æ –ö—É–Ωüíé" i="masero" AvatarURL={Config.Users.Masero.AvatarURL} />
                </DevelopersList>
            </div>
        );
    };
};

export default Developers;